import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi ,CityApi} from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'add-province.component.html'
})
export class AddProvinceComponent {
	@ViewChild('provinceName') name: NgModel;
	display: boolean = false; result: any;
	submitted: boolean = false;
	provinceName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	data: any;
	perPageLimit:number = 10;
	offset:number=0;
	total:number;

	constructor(private loopAuth: LoopBackAuth, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService,private cityApi:CityApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		
		this.rolename = this.userDetails.userDet.roles;
	}


	async submit(form) {
		this.submitted = true;
		let validFlag = true;
		if (form.valid) {

			//function call to change base64 format data to Image
			var dataArrObj = {
				"city_name": this.capital_letter(this.provinceName),
				"status": false
			}

			if(validFlag){
				this.loading=true;
				this.ifNameExist(dataArrObj).then((total)=>{
					if(total > 0){
						this.loading = false;
						this.result = "Province name already exist";
						this.display = true;
					}else{
						this.cityApi.upsert(dataArrObj).subscribe(
							data=>{
								this.loading=false;
								if(data){
									console.log(data);
									this.result = 'success';
									this.display=true;
								}else{
									this.display=true;
									this.result = 'Something went wrong.';
								}
							},
							err=>{
								console.log("err", err);
								this.loading=false;
							},
							()=>{
								this.loading=false;
							}
						)
					}
				
				});
			}
		}
	}

	ngOnInit(): void {

	}


	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				let access_token = this.loopAuth.getAccessTokenId();
				let cityNameString : string = dataObj.city_name;
				let cond = { "city_name": cityNameString};

				let api="/api/cities/count?where=";
				
				let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+access_token;
				this.http.get(url).subscribe((data)=>{
					console.log("Count ",data.json());
					resolve(data.json().count);
				},(err)=>{
					reject(err);
				});

			}catch(err){
				reject(err);
			}
		});
		
		
	}


	capital_letter(str) 
	{
		if(str != '' && str != null && str != undefined){
			str = str.split(" ");

			for (var i = 0, x = str.length; i < x; i++) {
				str[i] = str[i][0].toUpperCase() + str[i].substr(1);
			}
	
			return str.join(" ");
		}else{
			return str
		}
		
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["province"]);
	}

	
}


